header.banner.first {
  position: relative;
  background-color: $dark-color;
  height: 79vh;
  min-height: 25rem;
  width: 100%;
  overflow: hidden;
    & video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: 0;
    -ms-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
  }

}



header.banner .container {
  position: relative;
  z-index: 2;
}

header.banner .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: $roma_white;
  opacity: 0.95;
  z-index: 1;
}

@media (pointer: coarse) and (hover: none) {
  header.banner {
    background: url('https://source.unsplash.com/XT5OInaElMw/1600x900') $dark-color no-repeat center center scroll;
  }
  header.banner video {
    display: none;
  }
}

.products-header {
  margin-top: 5rem;
  text-align: center;
  padding: 0;
  & h1 {
    color: $red;
    font-family: 'PF Encore Sans Bold';
    font-size: 1.2rem;
    text-transform: uppercase;
  }
  &.grey {
    background-color: $dark-color;
  }
}

.breadcrumb-item.facetwp-selection-value {
  display: flex;

  // The separator between breadcrumbs (by default, a forward-slash: "/")
  + .breadcrumb-item {
    padding-left: $breadcrumb-item-padding;

    &::before {
      display: inline-block; // Suppress underlining of the separator in modern browsers
      padding-right: $breadcrumb-item-padding;
      color: $breadcrumb-divider-color;
      content: escape-svg($breadcrumb-divider-plus);
    }
  }
}
