section {
  &.hero {
      & .carousel-fade {
        & .carousel-item {
            text-align: center;
            height: 75vh;
            & .overlay-div {
               height:100%;
               width: 100%;
               position:absolute;
               background-color:$black;
               opacity:.5;
            }
            & .video-fluid {
              height: 100%;
              object-fit: cover;
              @extend .container-fluid;
            }
            & .carousel-caption {
              & h2 {
                font-family: 'ProximaNova-Extrabld';
                font-size: 7.556875rem;
                letter-spacing: .35rem;
              }
              & h3 {
                font-family: 'ProximaNova-Bold';
                font-size: 5rem;
                letter-spacing: .15rem;
              }
            }
        }
      }


  }
}

a {
  &.icon-collapse {
    border-bottom-left-radius: $icon_size * 2;
    border-bottom-right-radius: $icon_size * 2;
    height: $icon_size;
    width: $icon_size * 2;
    background: $primary;
    display: inline-block;
    &.accordion-toggle {
    &:before {
      /* symbol for "opening" panels */
      //  font-family: 'Glyphicons Halflings';  /* essential for enabling glyphicon */
      content: " ";    /* adjust as needed, taken from bootstrap.css */
      float: left;        /* adjust as needed */
      background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' fill='#{$white}' width='20' height='12.43' viewBox='0 0 20 12.43'><path d='M0.1,12.254c0.133,0.233,0.333,0.233,0.467,0L10,1.14l9.433,11.113c0.133,0.233,0.333,0.233,0.467,0
    		s0.133-0.583,0-0.817L10.233,0.176C10.167,0.06,10.067,0.001,10,0.001c-0.067,0-0.167,0.058-0.233,0.175L0.1,11.437
    		C-0.033,11.67-0.033,12.02,0.1,12.254z'/></svg>") ;         /* adjust as needed */
      width: 20px;
      height: 12.43px;
      background-position: bottom;
      background-repeat: no-repeat;
      margin: .5rem 0 0 1.38rem;
      }
      &.collapsed {
      &:before {
          /* symbol for "collapsed" panels */
          content: " ";    /* adjust as needed, taken from bootstrap.css */
          background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' fill='#{$white}' width='20' height='12.43' viewBox='0 0 20 12.43'>	<path fill='#FFFFFF' d='M0.1,0.176c0.133-0.233,0.333-0.233,0.467,0L10,11.29l9.433-11.113c0.133-0.233,0.333-0.233,0.467,0
          		s0.133,0.583,0,0.817l-9.667,11.261c-0.067,0.117-0.167,0.175-0.233,0.175c-0.067,0-0.167-0.058-0.233-0.175L0.1,0.993
          		C-0.033,0.76-0.033,0.41,0.1,0.176z'/></svg>") ;         /* adjust as needed */
              //  margin: .5rem 0rem 0rem 1.68rem;
      }
      }



  }
    & span.text {
      display: none;
    }
    & span.icon-collapse {


    }
  }
}

.overlayText {
 position:absolute;
  top:50%;
  //left:20%;
  z-index:1;
}

#topText {
  color: red;
  font-size: 20px;
  align-self: center;
}

.filters {
  background: $white;
  padding: 0;
  border: 1px solid $border-color;
  padding: 1rem;
}

.results {
  background: $white;

  border-top: 1px solid $border-color;
  border-left: 1px solid $border-color;
  border-right: 1px solid $border-color;
  padding: 1rem;
}

.stick:first-child{
  background: $light-color;
  padding: 3rem 0 0 0;
}

.woocommerce-cart {
  & .woocommerce-cart-form {
    @include make-container($gutter: $grid-gutter-width)
    & .cart_item {
      @include make-row($gutter: $grid-gutter-width)
    //  @include row-cols(6)
      border-bottom: 1px solid $border-light-color;
      & .product-thumbnail {
        //width: 15%;
        @extend .my-auto;
        @include make-col(3);
        & img {
          float: right;
          max-width: 50%;
        }
      }
      & .product-name {
        @extend .my-auto;
        @include make-col(3);
        & h3 {
          font-family: 'PF Encore Sans Pro Medium';
          font-size: 1.1875rem;
          //transform: scaleY(1.25);
          margin-bottom: 0;
        }
        & span.category {
          font-family: 'PF Encore Sans Light';
          font-size: .75rem;
          text-transform: uppercase;
          letter-spacing: .05rem;
        }
      }
      & .product-price {
        @extend .my-auto;
        @extend .text-center;
        @include make-col(1);
      }
      & .product-quantity {
        @extend .my-auto;
        @include make-col(1);
        & .quantity {
            border: 1px solid $border-light-color;
            border-radius: 2rem;
            padding: .5rem;
            text-align: center;
        }
      }
      & .product-subtotal {
        @extend .my-auto;
        @extend .text-center;
        @include make-col(1);
      }
      & .product-remove {
        @extend .my-auto;
        @include make-col(2);
        @extend .text-center;
      }

    }

    & .cart_total {
      @include make-row($gutter: $grid-gutter-width)
      & .coupon_form {
        @include make-col(6);
        @extend .my-auto;
        padding: 3rem 0;
        & .coupon {
          float: right;
          & label {
            display: none;
          }
        }
      }
      & .sum {
        @include make-col(2);
        @extend .text-right;
        @extend .my-auto;
      }
      & .total {
        @include make-col(1);
        @extend .text-center;
        @extend .my-auto;
      }
      & .sum_button{
        @include make-col(2);
        @extend .text-center;
        @extend .my-auto;
      }
    }

    & .product-subtotal, .total {

      & bdi {
        font-family: 'ProximaNova-Bold';
        font-size: 1rem;
        color: $orange;
      }
    }
    & .update_cart {
      border: 1px solid $border-light-color;
      border-radius: 2rem;
      padding: 1rem;
    }
    & tr.total {
      height: 150px;
    }
  }
  & .ico {
      &.search {
        height: 13.973px;
        width: 17.274px;
        background-image: url('../images/icon_reload.svg');
        transform: translateY(16%);
      }
  }
  & .cart-empty {
    text-align: center;
    padding: 4rem;
    & h2 {
      margin-bottom: 2.5rem;
    }
  }
}

.checkout_form {
  @include make-row($gutter: $grid-gutter-width);
}

.woocommerce-variation-add-to-cart {
  & span.pa {
    font-size: .8rem;
    margin: 0rem 0rem 0rem 1rem;
  }
}

.woocommerce-product-details__short-description {
  & p {
    margin-top: 1rem;
  }
}

.woocommerce-Tabs-panel {
  padding: 2rem 6rem;
}
